
import { defineComponent } from 'vue'
import { useI18n } from 'vue-i18n'

export default defineComponent({
  name: 'GoldInfo',
  setup() {
    const { t } = useI18n()
    return { t }
  },
  props: {
    value: {
      type: Number,
      required: false,
      default: 0
    },
    delta: {
      type: Boolean,
      required: false      
    },
  },
  computed: {
    visible() : boolean {
      return this.value != 0 || !this.delta
    },
    prefix() : string {
      if (this.delta && this.value > 0) {
        return '+'
      }
      else {
        return ''
      }
    }
  }
})
